/*Variable declarations*/
.Matrix{
  --bg-color: #bbbbbb;
  --bg-color-focus: #aaaaaa;
  --bg-color-hover: #cccccc;
  --bg-color-NaN: #aa2222;
}

.Matrix{
  border: solid;
  border-color: red;
  padding :10px;
  margin: 10px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.Row{
  border: solid;
  border-color: orange;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.Cell{
  border: solid;
  border-color: yellow;
  width: 80px;
  height: 30px;
  font-size: 20px;
  text-align: center;
  background-color: var(--bg-color);
  -webkit-transition: 0.1s;
  transition: 0.1s;
}

.Cell:focus {
  background-color: var(--bg-color-focus);
}

.Cell:disabled {
  color: black;
}

.NaN, .NaN:focus {
  background-color: var(--bg-color-NaN);
}

.changed:disabled {
  color: blue;
}

.userInput, .answer{
  border: solid;
  border-color: green;
  padding: 10px;
}

.shownWork, .ioPanel{
  border: solid;
  border-color: blue;
  padding: 10px;
  margin: 10px;
}
:myColors {
    --cell-color:#bbbbbb;
    --input-color:#aaaaaa;
    --text-color:#000000;
}

body {
    font-family: sans-serif;
    background-color:#888888;
    margin: 10px;
    height: 100%;
    width: 900px;
    overflow-x: hidden;
    overflow-y: auto;
    border: dashed;
}

.reactMade {
  background-color:#bbbbbb;
  border: solid;
  width:-webkit-fit-content;
  width:-moz-fit-content;
  width:fit-content;
  padding-right: 10px;
  padding-left: 10px;
  height:100%;
}

.myApp{
  border: solid;
  border-color: purple;
  margin: 10px;
  padding: 10px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}


